<template>
  <f7-page>
    <f7-navbar title="About Equality" back-link="Back"></f7-navbar>
    <f7-card>
      <f7-card-content>
        <p><strong>Equality</strong> ia a game based on the Riemann-Roch Theorem for Graphs.</p>
        <p>The goal of the game is for all nodes to have a positive number (including 0) in the least amount of moves possible.</p>

        <h4>How to</h4>
        <p>Tap on a node to spread values to its immediate children.</p>

        <iframe width="100%" height="315" src="https://www.youtube.com/embed/U33dsEcKgeQ?rel=0&amp;showinfo=0" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>

        <f7-button v-on:click="logout">Logout</f7-button>
      </f7-card-content>
    </f7-card>
  </f7-page>
</template>

<script>
  import {main} from '../service/main';

  export default {
    methods: {
      logout() {
        main.logout(this.$f7);
      },
    }
  }
</script>



