<template>
  <f7-page>
    <f7-navbar>
      <f7-nav-title>Equality</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>
    </f7-navbar>
    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">

        <f7-row class="levels-row">
          <f7-col width="50" class="pos-relative" v-if="levels" v-for="(grid, i) in levels" :key="i" v-on:click="goToLevel(i)">
            <img :src="images[i]" class="level-image" :alt="`Level ${i}`">
            <div class="level-chip"><span>level {{ i + 1 }}</span> <span v-html="getMovesCount(i) + '/' + getRank(i)"></span></div>
          </f7-col>
        </f7-row>

      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>

  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data() {
      return {
        cols: 4,
        rows: 6,
        levels: null,
        progress: [],
        score: [],
        images: [
          require('../images/0.png'),
          require('../images/1.png'),
          require('../images/2.png'),
          require('../images/3.png'),
          require('../images/4.png'),
          require('../images/5.png'),
          require('../images/6.png'),
          require('../images/7.png'),
          require('../images/8.png'),
          require('../images/9.png'),
          require('../images/10.png'),
          require('../images/11.png'),
          require('../images/12.png'),
          require('../images/13.png'),
          require('../images/14.png'),
          require('../images/15.png'),
          require('../images/16.png'),
          require('../images/17.png'),
          require('../images/18.png'),
          require('../images/19.png'),
          require('../images/20.png'),
          require('../images/21.png'),
          require('../images/22.png'),
          require('../images/23.png'),
          require('../images/24.png'),
          require('../images/25.png'),
          require('../images/26.png'),
          require('../images/27.png'),
          require('../images/28.png'),
          require('../images/29.png'),
          require('../images/30.png'),
          require('../images/31.png'),
          require('../images/32.png'),
          require('../images/33.png'),
          require('../images/34.png'),
          require('../images/35.png'),
        ]
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.$f7.dialog.close();

        this.getScore();

        this.levels = main.getLevels();
      })
    },
    methods: {
      getScore() {
        Firebase.database().ref('score').once('value').then(resp => this.score = resp.val());
      },

      goToLevel(key) {
        localStorage.removeItem('level');
        localStorage.removeItem('moves-' + key);

        this.$f7.views.current.router.navigate('/level/' + key);
      },

      getMovesCount(key) {
        if (!this.progress[key]) return '0';

        return `${this.progress[key].moves}`;
      },

      getRank(key) {
        let movesCount = this.progress && this.progress[key] ? parseInt(this.progress[key].moves) : 0;

        return this.score && this.score[key] ? `${movesCount - this.score[key].moves + 1}` : ' -';
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      }
    }
  }
</script>



