<template>
  <f7-page>
    <f7-navbar>
      <f7-nav-left>
        <f7-link icon-f7="arrow_left" text="Home" v-on:click="goHome"></f7-link>
      </f7-nav-left>
      <f7-nav-title>Equality Level <span v-if="levelId">{{getLevel}}</span></f7-nav-title>
      <f7-nav-right>
        <f7-link icon-f7="login" popover-open=".popover-menu"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">

        <div class="block">
          <f7-row>
            <f7-col class="text-color-white text-align-left"><p class="no-margin">{{moves}} Moves</p></f7-col>
            <f7-col class="text-color-white text-align-right"><p v-if="user && topMoves" class="no-margin">Top rank by {{ user.username }} / {{ topMoves }}</p></f7-col>
          </f7-row>
        </div>

        <div class="block block-insert">
          <f7-row v-if="grid" no-gap v-for="(cols, rowId) in grid" :key="rowId" class="v-row">
            <f7-col v-for="(col, colId) in cols" :key="colId" :class="'text-align-center v-col v-col-' + col">
              <svgCircle v-if="col[0]" :rowId="rowId" :colId="colId" :data="col[0]" :value="col[1]" :share="col[2]" :callback="updateCount"></svgCircle>
            </f7-col>
          </f7-row>
        </div>

      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>

    <f7-popover class="popover-menu">
      <f7-list>
        <f7-list-item v-on:click="resetPuzzle" title="Reset Puzzle"></f7-list-item>
      </f7-list>
    </f7-popover>
  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/database';
  import svgCircle from './components/svg-circle';
  import {main} from '../service/main';

  export default {
    components: {
      svgCircle,
    },
    data() {
      return {
        levelId: this.$f7route.params.levelId || 1,
        moves: 0,
        isWinner: null,
        grid: null,
        user: null,
        topMoves: null,
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getProgress();
      })
    },
    computed: {
      getLevel() {
        return parseInt(this.levelId) + 1;
      }
    },
    methods: {
      getProgress() {
        Firebase.database().ref(`score/${this.levelId}`).once('value').then(resp => {
          let score = resp.val();
          if (score) {
            this.topMoves = score.moves;
            Firebase.database().ref(`users/${score.userId}`).once('value').then(resp => this.user = resp.val());
          }
        });

        let puzzle = null;
        if (!localStorage.getItem(`level-${this.levelId}`)) {
          puzzle = {
            grid: JSON.parse(localStorage.getItem(`level-${this.levelId}`)),
            moves: 0
          };
        }

        this.setData(puzzle);
      },

      setData(puzzle) {
        this.grid = puzzle ? puzzle.grid : main.getLevel(this.levelId);
        this.moves = puzzle ? parseInt(puzzle.moves) : 0;
        this.isWinner = main.checkIfWon(this.grid);

        localStorage.setItem(`level-${this.levelId}`, JSON.stringify(this.grid));
      },

      updateCount(cell, data, value) {
        this.moves++;
        let grid = JSON.parse(localStorage.getItem(`level-${this.levelId}`));
        let len = data.length;

        // clicked cell
        grid[cell[0]][cell[1]][1] = value - len;

        // adjacent cells
        for (let i = 0; i < data.length; i++) {
          grid[data[i][0]][data[i][1]][1] += 1;
        }

        this.isWinner = main.checkIfWon(grid);

        // if won, show message and go to next level
        if (this.isWinner) {
          console.log(this.isWinner);
          main.saveScore(this.levelId, this.moves).then();

          this.$f7.dialog.alert('All nodes have a positive balance :)', 'You won!', () => {
            this.$f7.views.current.router.navigate('/level/' + (parseInt(this.levelId) + 1));
          });
        }

        localStorage.setItem(`level-${this.levelId}`, JSON.stringify(grid));

        this.grid = grid;
      },

      resetPuzzle() {
        let grid = main.getLevel(this.levelId);
        this.isWinner = false;
        this.moves = 0;
        this.grid = grid;

        localStorage.setItem(`level-${this.levelId}`, JSON.stringify(grid));

        this.$f7.dialog.alert('Level reset compete');
        this.$f7.popover.close();

        this.$f7.views.current.router.navigate('/level/' + this.levelId, {
          pushState: true
        });
      },

      goHome() {
        this.$f7.views.current.router.navigate('/');
      },
    }
  }
</script>



